const re_yyyy = /yyyy/;
const re_MM = /MM/;
const re_dd = /dd/;
const re_hh = /hh/;
const re_mm = /mm/;
const re_ss = /ss/;

export const unixTimeToDateFormat = (unixtime, format = "yyyy-MM-dd hh:mm:ss") => {
  const d = new Date(unixtime);
  const year = d.getFullYear();
  const month = d.getMonth() + 1;
  const day = d.getDate();
  const hour = ('0' + d.getHours()).slice(-2);
  const min = ('0' + d.getMinutes()).slice(-2);
  const sec = ('0' + d.getSeconds()).slice(-2);
  return format.replace(re_yyyy, year).replace(re_MM,month).replace(re_dd,day).replace(re_hh,hour).replace(re_mm,min).replace(re_ss,sec);
}
export const getNowUnixtime = () => {
  new Date().getTime();
}
